
.filter .filter-box {
  width: 50%;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
  padding: 15px;
  border-radius: 5px;
}

.filter .filter-box .search {
  display: flex;
}

.filter .filter-box .search input {
  width: 70%;
  margin-right: auto;
}

.filter .filter-box .search select {
  width: 25%;
}

.filter .filter_group {
  width: 50%;
}

.filter .filter_label {
  margin-right: 10px;
  margin-bottom: 0;
}

@media (max-width: 413px) and (min-width: 300px) {
  .filter .filter-box {
    width: 100%;
  }

  .filter .filter-box .search {
    display: block;
  }

  .filter .filter-box .search input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .filter .filter-box .search select {
    width: 100%;
  }
}
