.base-dark-bg {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
}

.MuiBox-root
{
  position: relative !important;
  z-index: 1;
}

.form-bg {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.form-bg form {
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
  overflow-y: auto;
  max-height: 500px;
  width: 380px;
}

.form-bg form .form-bg_icon {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.form-bg form .form-bg_input-group {
  width: 50%;
  display: inline-block;
  padding: 0 5px;
}

.form-bg .form-bg_two-input {
  width: 600px;
  max-height: 800px;
  overflow-y: auto;
}

