.show_side-content {
  -webkit-animation: show_side-content 1s linear forwards;
          animation: show_side-content 1s linear forwards;
}

.hide_side-content {
  -webkit-animation: hide_side-content 1s linear forwards;
          animation: hide_side-content 1s linear forwards;
}

.hide_sidebar {
  -webkit-animation: hide_sidebar 1s linear forwards;
          animation: hide_sidebar 1s linear forwards;
}

.show_sidebar {
  -webkit-animation: show_sidebar 1s forwards;
          animation: show_sidebar 1s forwards;
}

@-webkit-keyframes hide_sidebar {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
  }
}

@keyframes hide_sidebar {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
  }
}

@-webkit-keyframes show_sidebar {
  from {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes show_sidebar {
  from {
    -webkit-transform: translateX(-1000px);
            transform: translateX(-1000px);
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes show_side-content {
  from {
    -webkit-transform: inherit;
            transform: inherit;
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    width: 100%;
  }
}

@keyframes show_side-content {
  from {
    -webkit-transform: inherit;
            transform: inherit;
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    width: 100%;
  }
}

@-webkit-keyframes hide_side-content {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    width: 100%;
  }
  to {
    -webkit-transform: inherit;
            transform: inherit;
    width: 75%;
  }
}

@keyframes hide_side-content {
  from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    width: 100%;
  }
  to {
    -webkit-transform: inherit;
            transform: inherit;
    width: 75%;
  }
}

