.chat-box {
  background: white;
  width: 100%;
  height: 100%;
}

.chat-box .chat-box_header {
  width: 100%;
  height: 8%;
  background: #f8fafc;
  border-bottom: 1px solid #e2e8f0;
}

.chat-box .chat-box_body {
  width: 100%;
  height: 82%;
  max-height: 82%;
  overflow-y: auto;
  position: relative;
  display: block;
  padding: 10px;
}

.chat-box .chat-box_footer {
  width: 100%;
  height: 10%;
  background: #f8fafc;
  border-top: 1px solid #e2e8f0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
}

.chat-box .chat-box_footer input {
  width: 90%;
}

.chat-box .chat-box_footer .chat-box_footer_icon {
  font-size: 18px;
  cursor: pointer;
}

.chat-box .chat-box_footer .chat-box_footer_icon:hover {
  color: #1976d2;
  transition: 0.5s;
}

@media (max-width: 1000px) and (min-width: 300px) {
  .chat-box .chat-box_header {
    height: 10%;
  }
  .chat-box .chat-box_body {
    height: 80%;
  }
  .chat-box {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 10;
    height: 90%;
  }
}
